@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200&display=swap);

body,html{padding:0;margin:0;font-family:'Montserrat', sans-serif;scroll-behavior:smooth}.logo-dots{margin-top:-10px;width:50px;height:50px}.app-root{text-align:center}.app-header{margin-top:20px;padding:10px;color:white;font-size:20pt}.app-title{margin-bottom:10px}.app-sub-tag{font-size:14px;margin:0px}.app-input{line-height:30px;border:1px solid #ccc;border-radius:4px;font-size:14px;padding:0 8px;color:#666;width:400px;margin-bottom:30px}.enter-name-form{border:1px solid black;margin:100px;padding:20px;border-radius:4px}.name-input{border-radius:4px;padding:3px;border:0.5px solid gray;margin:8px 3px 8px 3px}.submit-button{color:black;padding:5px;border-radius:5px;border:1px;box-shadow:0px -1px 1px rgba(0,0,0,0.08),1px 0px 1px rgba(0,0,0,0.08),-1px 0px 1px rgba(0,0,0,0.08),0px 1px 1px rgba(0,0,0,0.08);width:140.5px;margin-top:5px;margin-bottom:5px;cursor:pointer}.submit-button:hover{box-shadow:0px 4px 6px rgba(0,0,0,0.15),4px 4px 2px rgba(0,0,0,0.15)}.card{border:none;width:140px;height:120px;margin:5px;border-radius:4px}.card:hover{transform:scale(1.1);box-shadow:0px 4px 6px rgba(12,12,12,0.15),4px 4px 2px rgba(0,0,0,0.15);background-color:rgba(0,0,0,0.2);transition:all 300ms ease;cursor:pointer}.card-title{border-radius:5px;max-width:80%;padding:12px 10px;color:#fff;font-size:16px;text-shadow:0 0 3px rgba(0,0,0,0.3);font-weight:900;letter-spacing:.05em}.cards{display:inline-flex}

.image-root{background-size:cover;background-position:center center;display:inline-block;vertical-align:top;box-sizing:border-box;position:relative;border:1px solid white;flex-grow:1;border:3px solid red}.image-root>.image-icon-section{visibility:hidden;background:rgba(0,0,0,0.7);cursor:pointer;position:absolute;width:100%;height:100%;top:0;left:0;color:white;padding:15px;text-align:center;box-sizing:border-box;white-space:pre;display:flex;align-items:center;justify-content:center}.image-root:hover>.image-icon-section{visibility:visible;background:rgba(0,0,0,0.7)}.image-root .image-icon{font-size:20px;vertical-align:middle;border:1px solid #ccc;color:#ccc;border-radius:5px;cursor:pointer;padding:12px;margin:3px}.image-root .image-icon:hover{color:white;border-color:white}

.expanded-img{max-width:80%;max-height:80%;display:block;margin:auto;margin-bottom:5px}.close-button{margin-bottom:5px;display:block;border-radius:4px;border:0.5px solid black;color:black;cursor:pointer;width:100px;font-size:12px;text-decoration:none;display:inline-block}.close-button:hover{background-color:#5c5c5c;color:white}.ReactModal__Overlay{opacity:0;transition:opacity 700ms ease-in-out}.ReactModal__Overlay--after-open{opacity:1}.ReactModal__Overlay--before-close{opacity:0}.modal{text-align:center}

.gallery-root{text-align:center}.gallery-header{background-color:#222;padding:10px;color:white;font-size:20pt}.gallery-intro{font-size:15pt}.displaying-label{font-size:12px;font-weight:bolder;color:#1052AA;display:flex;margin-top:10px}

.nav-bar{position:fixed;top:0;display:flex;background-color:white;align-items:center;justify-content:space-between;height:45px;width:-webkit-fill-available;z-index:9999}.left-header{display:inline-flex}.logo-image{width:100px;margin-left:5px}.name-nav-bar{font-size:12px;margin-right:20px;color:black}.link-button{border:none;background-color:none !important;margin-right:5px;color:black;margin-top:17px;font-size:10px;font-weight:bold}.search{margin-right:10px}@keyframes example{0%{color:black;left:0px;top:0px}25%{color:#1463DC;left:200px;top:0px}50%{color:#FF1684;left:200px;top:200px}75%{color:#1463DC;left:0px;top:200px}100%{color:black;left:0px;top:0px}}.join-flickr{animation-name:example;animation-duration:4s;animation-direction:alternate;animation-iteration-count:infinite}

